'use client'
import React from 'react'

interface FormularioNuevoProductoProps {
  onAgregar: (producto: any) => void
  onCancelar: () => void
}

export default function FormularioNuevoProducto({ onAgregar, onCancelar }: FormularioNuevoProductoProps) {
  const [nuevo, setNuevo] = React.useState({
    nombre: '',
    descripcion: '',
    precioUnitario: '',
    idStock: ''
  })

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setNuevo({ ...nuevo, [e.target.name]: e.target.value })
  }

  const handleSubmit = () => {
    if (!nuevo.nombre || !nuevo.precioUnitario || !nuevo.idStock) return
    onAgregar({
      ...nuevo,
      idProducto: Date.now(),
      precioUnitario: parseFloat(nuevo.precioUnitario),
      idStock: parseInt(nuevo.idStock),
      precioTotal: parseFloat(nuevo.precioUnitario) * parseInt(nuevo.idStock),
      idTipoProducto: 0,
      idProveedor: 0,
      fechaRegistro: new Date().toISOString().split('T')[0]
    })
  }

  return (
    <div className="filters-section">
      <input name="nombre" placeholder="Nombre" value={nuevo.nombre} onChange={handleChange} />
      <input name="descripcion" placeholder="Descripción" value={nuevo.descripcion} onChange={handleChange} />
      <input name="precioUnitario" type="number" placeholder="Precio" value={nuevo.precioUnitario} onChange={handleChange} />
      <input name="idStock" type="number" placeholder="Stock" value={nuevo.idStock} onChange={handleChange} />
      <button onClick={handleSubmit} className="primary-btn">Guardar</button>
      <button onClick={onCancelar} className="export-btn">Cancelar</button>
    </div>
  )
}
