'use client'
import React from 'react'
import HeaderProductos from '../../components/productos/HeaderProductos'
import FiltrosProductos from '../../components/productos/FiltrosProductos'
import TablaProductos from '../../components/productos/TablaProductos'
import FormularioNuevoProducto from '../../components/productos/FormularioNuevoProducto'
import '@/styles/productos.css'

export default function GestionDeProductosPage() {
  const [productos, setProductos] = React.useState([
    {
      Producto: "Laptop HP EliteBook",
      idProducto: 1,
      nombre: "Laptop HP EliteBook",
      descripcion: "Core i7 16GB RAM 512GB SSD, 14\" FHD",
      precioUnitario: 4899.99,
      precioTotal: 73499.85,
      idStock: 15,
      idTipoProducto: 101,
      idProveedor: 201,
      fechaRegistro: "2025-06-01"
    },
    {
      Producto: "Monitor Dell 27\" 4K",
      idProducto: 2,
      nombre: "Monitor Dell 27\" 4K",
      descripcion: "U2723QE, IPS, USB-C",
      precioUnitario: 2299.99,
      precioTotal: 18399.92,
      idStock: 8,
      idTipoProducto: 102,
      idProveedor: 202,
      fechaRegistro: "2025-06-02"
    }
  ])

  const [mostrarFormulario, setMostrarFormulario] = React.useState(false)

  const agregarProducto = (nuevoProducto: any) => {
    setProductos([...productos, nuevoProducto])
    setMostrarFormulario(false)
  }

  return (
    <div className="dashboard-container">
      <HeaderProductos 
        total={productos.length} 
        onNuevoProductoClick={() => setMostrarFormulario(true)} 
      />
      <FiltrosProductos />
      {mostrarFormulario && (
        <FormularioNuevoProducto 
          onAgregar={agregarProducto}
          onCancelar={() => setMostrarFormulario(false)}
        />
      )}
      <TablaProductos productos={productos} />
    </div>
  )
}
